.divStyle {
    display: flex;
    flex-wrap: wrap;
    height: 72vh;
    width: 90vw;
    margin: auto;
    border: solid;
    overflow: hidden;
    overflow-y: scroll;
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
    scrollbar-width: none;  /* Firefox */
}
.divStyle::-webkit-scrollbar {
    display: none;  /* Safari and Chrome */
}