.popup-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.popup {
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    width: 40vw;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

.popup-container {
    backdrop-filter: blur(5px);
}